"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { toast } from 'sonner'

export function VisualHealing() {
  const [isHealing, setIsHealing] = useState(false)
  const [healingProgress, setHealingProgress] = useState(0)
  const [healingStep, setHealingStep] = useState('')

  const simulateHealing = async () => {
    setIsHealing(true)
    setHealingProgress(0)
    
    const steps = [
      { step: 'Taking screenshot...', progress: 20 },
      { step: 'Analyzing DOM changes...', progress: 40 },
      { step: 'Running computer vision detection...', progress: 60 },
      { step: 'Finding new element location...', progress: 80 },
      { step: 'Updating test locators...', progress: 100 }
    ]

    for (const { step, progress } of steps) {
      setHealingStep(step)
      setHealingProgress(progress)
      await new Promise(resolve => setTimeout(resolve, 1000))
    }

    toast.success('Visual healing completed successfully!')
    setIsHealing(false)
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold">Visual Self-Healing Engine</h2>
          <p className="text-muted-foreground">AI-powered UI test repair using computer vision</p>
        </div>
        <Badge variant="secondary">YOLOv8 + OpenCV</Badge>
      </div>

      <Tabs defaultValue="healing" className="space-y-4">
        <TabsList>
          <TabsTrigger value="healing">Healing Engine</TabsTrigger>
          <TabsTrigger value="history">Healing History</TabsTrigger>
          <TabsTrigger value="config">Configuration</TabsTrigger>
        </TabsList>

        <TabsContent value="healing" className="space-y-4">
          <div className="grid gap-4 md:grid-cols-2">
            <Card>
              <CardHeader>
                <CardTitle>Healing Process</CardTitle>
                <CardDescription>Multi-layer recovery system for broken UI tests</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <div className="flex justify-between text-sm">
                    <span>1. DOM Similarity Matching</span>
                    <Badge variant="outline">Active</Badge>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span>2. Computer Vision Detection</span>
                    <Badge variant="outline">Active</Badge>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span>3. Semantic Analysis</span>
                    <Badge variant="outline">Active</Badge>
                  </div>
                </div>
                
                <Button 
                  onClick={simulateHealing} 
                  disabled={isHealing}
                  className="w-full"
                >
                  {isHealing ? 'Healing in Progress...' : 'Start Visual Healing'}
                </Button>

                {isHealing && (
                  <div className="space-y-2">
                    <div className="flex justify-between text-sm">
                      <span>{healingStep}</span>
                      <span>{healingProgress}%</span>
                    </div>
                    <Progress value={healingProgress} />
                  </div>
                )}
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Current Status</CardTitle>
                <CardDescription>Real-time healing engine metrics</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-3">
                  <div className="flex justify-between">
                    <span className="text-sm">Success Rate</span>
                    <span className="text-sm font-medium text-green-600">94.2%</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Avg Healing Time</span>
                    <span className="text-sm font-medium">2.3s</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Elements Healed Today</span>
                    <span className="text-sm font-medium">47</span>
                  </div>
                  <div className="flex justify-between">
                    <span className="text-sm">Active Monitors</span>
                    <span className="text-sm font-medium">12</span>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          <Card>
            <CardHeader>
              <CardTitle>Healing Workflow Simulation</CardTitle>
              <CardDescription>See how the visual healing engine works</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                <div className="bg-muted p-4 rounded-lg">
                  <h4 className="font-medium mb-2">Scenario: Checkout Button Moved</h4>
                  <div className="space-y-2 text-sm">
                    <div className="flex items-center space-x-2">
                      <div className="w-2 h-2 bg-red-500 rounded-full"></div>
                      <span>Test fails: "Can't find checkout button"</span>
                    </div>
                    <div className="flex items-center space-x-2">
                      <div className="w-2 h-2 bg-yellow-500 rounded-full"></div>
                      <span>Healer triggered: Taking screenshot</span>
                    </div>
                    <div className="flex items-center space-x-2">
                      <div className="w-2 h-2 bg-blue-500 rounded-full"></div>
                      <span>AI Vision: Found "checkout button" at X:120, Y:250</span>
                    </div>
                    <div className="flex items-center space-x-2">
                      <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                      <span>Success: Updated locator and test passed</span>
                    </div>
                  </div>
                </div>

                <div className="grid gap-4 md:grid-cols-3">
                  <div className="text-center p-4 border rounded-lg">
                    <div className="text-2xl font-bold text-blue-600">2.3s</div>
                    <div className="text-sm text-muted-foreground">Average Healing Time</div>
                  </div>
                  <div className="text-center p-4 border rounded-lg">
                    <div className="text-2xl font-bold text-green-600">94%</div>
                    <div className="text-sm text-muted-foreground">Success Rate</div>
                  </div>
                  <div className="text-center p-4 border rounded-lg">
                    <div className="text-2xl font-bold text-purple-600">3</div>
                    <div className="text-sm text-muted-foreground">Healing Layers</div>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="history" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Recent Healing Activities</CardTitle>
              <CardDescription>Track all visual healing operations</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {[
                  {
                    time: '2 minutes ago',
                    element: 'Login Button',
                    issue: 'Element moved to new position',
                    solution: 'Updated XPath selector',
                    status: 'success'
                  },
                  {
                    time: '15 minutes ago',
                    element: 'Search Input',
                    issue: 'ID attribute changed',
                    solution: 'Found by placeholder text',
                    status: 'success'
                  },
                  {
                    time: '1 hour ago',
                    element: 'Submit Form',
                    issue: 'Button text changed',
                    solution: 'Located by semantic analysis',
                    status: 'success'
                  },
                  {
                    time: '3 hours ago',
                    element: 'Navigation Menu',
                    issue: 'Structure completely changed',
                    solution: 'Manual intervention required',
                    status: 'failed'
                  }
                ].map((item, index) => (
                  <div key={index} className="flex items-center space-x-4 p-3 border rounded-lg">
                    <div className={`w-3 h-3 rounded-full ${
                      item.status === 'success' ? 'bg-green-500' : 'bg-red-500'
                    }`}></div>
                    <div className="flex-1">
                      <div className="flex justify-between items-start">
                        <div>
                          <p className="font-medium">{item.element}</p>
                          <p className="text-sm text-muted-foreground">{item.issue}</p>
                          <p className="text-sm text-blue-600">{item.solution}</p>
                        </div>
                        <div className="text-right">
                          <Badge variant={item.status === 'success' ? 'default' : 'destructive'}>
                            {item.status}
                          </Badge>
                          <p className="text-xs text-muted-foreground mt-1">{item.time}</p>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="config" className="space-y-4">
          <div className="grid gap-4 md:grid-cols-2">
            <Card>
              <CardHeader>
                <CardTitle>Healing Settings</CardTitle>
                <CardDescription>Configure visual healing behavior</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <label className="text-sm font-medium">Healing Timeout (seconds)</label>
                  <input 
                    type="number" 
                    defaultValue={30} 
                    className="w-full p-2 border rounded"
                  />
                </div>
                <div className="space-y-2">
                  <label className="text-sm font-medium">Max Retry Attempts</label>
                  <input 
                    type="number" 
                    defaultValue={3} 
                    className="w-full p-2 border rounded"
                  />
                </div>
                <div className="space-y-2">
                  <label className="text-sm font-medium">Confidence Threshold</label>
                  <input 
                    type="range" 
                    min="0.5" 
                    max="1" 
                    step="0.1" 
                    defaultValue={0.8} 
                    className="w-full"
                  />
                  <div className="text-xs text-muted-foreground">80%</div>
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>AI Model Settings</CardTitle>
                <CardDescription>Computer vision model configuration</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <label className="text-sm font-medium">Vision Model</label>
                  <select className="w-full p-2 border rounded">
                    <option>YOLOv8 (Recommended)</option>
                    <option>YOLOv5</option>
                    <option>Custom Model</option>
                  </select>
                </div>
                <div className="space-y-2">
                  <label className="text-sm font-medium">Detection Classes</label>
                  <div className="space-y-1">
                    {['button', 'input', 'link', 'image', 'text'].map(cls => (
                      <label key={cls} className="flex items-center space-x-2">
                        <input type="checkbox" defaultChecked />
                        <span className="text-sm">{cls}</span>
                      </label>
                    ))}
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>
    </div>
  )
}
