"use client"

import { useState } from 'react'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Button } from '@/components/ui/button'
import { Textarea } from '@/components/ui/textarea'
import { Badge } from '@/components/ui/badge'
import { Progress } from '@/components/ui/progress'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { toast } from 'sonner'

export function NLPTestGenerator() {
  const [requirement, setRequirement] = useState('')
  const [isGenerating, setIsGenerating] = useState(false)
  const [generatedTest, setGeneratedTest] = useState('')
  const [systemPrompt, setSystemPrompt] = useState(`You are an expert test automation engineer. Convert natural language requirements into executable Playwright test scripts.

Guidelines:
- Generate complete, runnable test code
- Include proper selectors and assertions
- Add realistic test data
- Handle common edge cases
- Use modern async/await patterns
- Include descriptive test names and comments

Output format: Complete JavaScript/TypeScript test file with imports and proper structure.`)

  const handleGenerate = async () => {
    if (!requirement.trim()) {
      toast.error('Please enter a test requirement')
      return
    }

    setIsGenerating(true)
    
    try {
      // Simulate AI processing
      await new Promise(resolve => setTimeout(resolve, 2000))
      
      // Mock generated test based on requirement
      const mockTest = generateMockTest(requirement)
      setGeneratedTest(mockTest)
      
      toast.success('Test generated successfully!')
    } catch (error) {
      toast.error('Failed to generate test')
    } finally {
      setIsGenerating(false)
    }
  }

  const generateMockTest = (req: string) => {
    const isLoginTest = req.toLowerCase().includes('login')
    const isCheckoutTest = req.toLowerCase().includes('checkout')
    const isPayPalTest = req.toLowerCase().includes('paypal')

    if (isLoginTest) {
      return `import { test, expect } from '@playwright/test';

test.describe('User Login Tests', () => {
  test('should login with valid credentials', async ({ page }) => {
    // Navigate to login page
    await page.goto('/login');
    
    // Fill login form
    await page.fill('[data-testid="email-input"]', 'user@example.com');
    await page.fill('[data-testid="password-input"]', 'password123');
    
    // Click login button
    await page.click('[data-testid="login-button"]');
    
    // Verify successful login
    await expect(page).toHaveURL('/dashboard');
    await expect(page.locator('[data-testid="user-menu"]')).toBeVisible();
  });

  test('should show error for invalid credentials', async ({ page }) => {
    await page.goto('/login');
    
    await page.fill('[data-testid="email-input"]', 'invalid@example.com');
    await page.fill('[data-testid="password-input"]', 'wrongpassword');
    await page.click('[data-testid="login-button"]');
    
    await expect(page.locator('[data-testid="error-message"]')).toBeVisible();
    await expect(page.locator('[data-testid="error-message"]')).toContainText('Invalid credentials');
  });
});`
    }

    if (isCheckoutTest && isPayPalTest) {
      return `import { test, expect } from '@playwright/test';

test.describe('Guest Checkout with PayPal', () => {
  test('should complete guest checkout using PayPal', async ({ page }) => {
    // Add item to cart
    await page.goto('/products/sample-product');
    await page.click('[data-testid="add-to-cart"]');
    
    // Go to checkout as guest
    await page.goto('/cart');
    await page.click('[data-testid="checkout-button"]');
    await page.click('[data-testid="guest-checkout"]');
    
    // Fill shipping information
    await page.fill('[data-testid="first-name"]', 'John');
    await page.fill('[data-testid="last-name"]', 'Doe');
    await page.fill('[data-testid="email"]', 'john.doe@example.com');
    await page.fill('[data-testid="address"]', '123 Main St');
    await page.fill('[data-testid="city"]', 'New York');
    await page.fill('[data-testid="zip"]', '10001');
    
    // Select PayPal payment
    await page.click('[data-testid="payment-paypal"]');
    
    // Mock PayPal flow (in real scenario, handle PayPal popup)
    await page.click('[data-testid="paypal-button"]');
    
    // Verify order completion
    await expect(page).toHaveURL(/.*\/order-confirmation/);
    await expect(page.locator('[data-testid="order-success"]')).toBeVisible();
  });
});`
    }

    return `import { test, expect } from '@playwright/test';

test.describe('Generated Test', () => {
  test('should perform the requested action', async ({ page }) => {
    // Navigate to the application
    await page.goto('/');
    
    // Add your test steps here based on: ${req}
    // This is a template - customize based on specific requirements
    
    // Example assertions
    await expect(page).toHaveTitle(/.*App.*/);
    await expect(page.locator('body')).toBeVisible();
  });
});`
  }

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold">NLP Test Generator</h2>
          <p className="text-muted-foreground">Convert plain English requirements into executable test scripts</p>
        </div>
        <Badge variant="secondary">AI-Powered</Badge>
      </div>

      <Tabs defaultValue="generator" className="space-y-4">
        <TabsList>
          <TabsTrigger value="generator">Test Generator</TabsTrigger>
          <TabsTrigger value="prompt">System Prompt</TabsTrigger>
          <TabsTrigger value="examples">Examples</TabsTrigger>
        </TabsList>

        <TabsContent value="generator" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Describe Your Test Requirement</CardTitle>
              <CardDescription>
                Enter your test requirement in plain English. The AI will generate a complete test script.
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <Textarea
                placeholder="Example: Test guest checkout with PayPal payment method"
                value={requirement}
                onChange={(e) => setRequirement(e.target.value)}
                rows={4}
              />
              <Button 
                onClick={handleGenerate} 
                disabled={isGenerating}
                className="w-full"
              >
                {isGenerating ? 'Generating Test...' : 'Generate Test Script'}
              </Button>
              {isGenerating && (
                <div className="space-y-2">
                  <div className="flex justify-between text-sm">
                    <span>Processing requirement...</span>
                    <span>AI Analysis</span>
                  </div>
                  <Progress value={33} />
                </div>
              )}
            </CardContent>
          </Card>

          {generatedTest && (
            <Card>
              <CardHeader>
                <CardTitle>Generated Test Script</CardTitle>
                <CardDescription>
                  Review and customize the generated test before saving
                </CardDescription>
              </CardHeader>
              <CardContent>
                <pre className="bg-muted p-4 rounded-lg overflow-x-auto text-sm">
                  <code>{generatedTest}</code>
                </pre>
                <div className="flex gap-2 mt-4">
                  <Button variant="outline">Copy Code</Button>
                  <Button variant="outline">Save Test</Button>
                  <Button>Run Test</Button>
                </div>
              </CardContent>
            </Card>
          )}
        </TabsContent>

        <TabsContent value="prompt" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>System Prompt Configuration</CardTitle>
              <CardDescription>
                Customize the AI behavior by modifying the system prompt
              </CardDescription>
            </CardHeader>
            <CardContent className="space-y-4">
              <Textarea
                value={systemPrompt}
                onChange={(e) => setSystemPrompt(e.target.value)}
                rows={12}
                className="font-mono text-sm"
              />
              <Button variant="outline">Save Prompt</Button>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="examples" className="space-y-4">
          <div className="grid gap-4 md:grid-cols-2">
            <Card>
              <CardHeader>
                <CardTitle>Login Test Example</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-sm text-muted-foreground mb-2">Input:</p>
                <p className="text-sm bg-muted p-2 rounded">"Test user login with valid and invalid credentials"</p>
                <p className="text-sm text-muted-foreground mt-4 mb-2">Generated:</p>
                <p className="text-sm">Complete Playwright test with form filling, assertions, and error handling</p>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>E-commerce Example</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-sm text-muted-foreground mb-2">Input:</p>
                <p className="text-sm bg-muted p-2 rounded">"Test guest checkout with PayPal payment"</p>
                <p className="text-sm text-muted-foreground mt-4 mb-2">Generated:</p>
                <p className="text-sm">End-to-end checkout flow with payment integration</p>
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>
    </div>
  )
}
